.animate {
  animation: fade-in-down 600ms;
  animation-delay: 200ms;
}

@include keyframes(fade-in-down) {
  0% {
    opacity: 0;
    transform: translateY(-10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@include keyframes(fade){
  0%{
    opacity:0;
  }
  60%{
    opacity:0;
  }
  100%{
    opacity:1;
  }
}